import Mock from "mockjs";

const mock = [
  {
    url: "/api/role/page",
    type: "post",
    handler({ body }) {
      const { pageIndex, pageSize = 10 } = JSON.parse(body);

      return {
        result: {
          records: [...Array(pageSize)].map((_, i) =>
            Mock.mock({
              id: "@guid",
              roleName: `-${pageIndex * pageSize - pageSize + i + 1}`,
              description: "@csentence",
              "status|1": [0, 1],
            })
          ),
          total: 100,
          pageIndex,
        },
        status: "success",
        msg: "",
      };
    },
  },
  {
    // 
    url: "/api/role/status/update",
    type: "post",
    handler() {
      // 
      const b = Mock.mock({ "boolean|1-2": false }).boolean;
      return {
        result: null,
        status: b ? "success" : "failure",
        msg: b ? "" : "",
      };
    },
  },
  {
    // 
    url: "/api/role/add",
    type: "post",
    handler() {
      // 
      const b = Mock.mock({ "boolean|1-2": false }).boolean;
      return {
        result: null,
        status: b ? "success" : "failure",
        msg: b ? "" : "",
      };
    },
  },
  {
    // 
    url: "/api/role/update",
    type: "post",
    handler() {
      // 
      const b = Mock.mock({ "boolean|1-2": false }).boolean;
      return {
        result: null,
        status: b ? "success" : "failure",
        msg: b ? "" : "",
      };
    },
  },
];

export default mock;
