import { createSlice } from "@reduxjs/toolkit";

export const systemSlice = createSlice({
  name: "system",
  initialState: {
    // 
    theme: localStorage.getItem("theme") || "light", // or dark

    // 
    menu: [],
  },
  reducers: {
    // 
    setTheme: (state, action) => {
      const theme =
        action.payload ||
        (() => {
          if (state.theme === "dark") {
            return "light";
          } else {
            return "dark";
          }
        })();
      state.theme = theme;
      localStorage.setItem("theme", theme);
    },
    setMenu: (state, action) => {
      state.menu = action.payload;
    },
  },
});
//  case reducer  Action creators
export const { setTheme, setMenu } = systemSlice.actions;

export const selectTheme = (state) => state.system.theme;

export const selectMenu = (state) => state.system.menu;

export default systemSlice.reducer;
